.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.blue {color: #06538b;}

.word-wrap{word-wrap: break-word;}

.divider {
  height: 1px;
  width:100%;
  display:block;
  margin: 9px 0;
  overflow: hidden;
  background-color: #e5e5e5;
}

.image {
    cursor: pointer;
    opacity: 1;
    display: block;
    width: 100%;
    height: auto;
    transition: .5s ease;
    backface-visibility: hidden;
}

.middle {
    cursor: pointer;
    transition: .5s ease;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
}

.title {
    line-height: 1.05;
    font-weight: 600 !important;
    transition: all .2s ease-in-out;
}

.author-nick {
    color: #606060;
}

.film-views {
    line-height: 0.6;
    color: #606060;
}


.button-my{
    margin:1px;
}

.button-my:focus {
    outline: none !important;
    outline-offset: none !important;
}


.playlist-add-icon {
    cursor: pointer;
}

.playlist-add-icon-holder {
    cursor: pointer;
}

.centerd {
    display:table-cell;
    vertical-align:middle;
    text-align:center;
}

@media (hover: hover) {

    .container:hover .image {
        opacity: 0.5;
    }

    .container:hover .middle {
        opacity: 1;
    }

    .playlist-add-icon {
        transition: .2s ease;
        opacity: 0;
    }

    .film-preview-holder:hover .playlist-add-icon {
        opacity: 0.6;
    }

    .film-preview-holder:hover .playlist-add-icon-holder:hover .playlist-add-icon{
        opacity: 1;
    }

    .playlist-remove-holder {
        opacity: 0;
        transition: all .2s ease-in-out;
        color: #606060;
    }

    .playlist-remove-holder:hover {
        opacity: 1;
        cursor: pointer;
    }

    .playlist-remove-container:hover .playlist-remove-holder  {
        opacity: 0.6;
    }

    .playlist-remove-container:hover .playlist-remove-holder:hover  {
        opacity: 1;
    }

}

@media (hover: none) {
    .playlist-add-icon {
        opacity: 0.7;

    }
}


